<template>
  <DxBox :height="220" direction="row" width="100%">
    <DxItem :ratio="4">
      <template #default>
        <div class="rect1">
          <DxSelectBox :data-source="query_types" :label-mode="'hidden'" v-model:value="query_type" display-expr="name"
            :height="50" />
        </div>
      </template>
    </DxItem>
    <DxItem :ratio="5">
      <template #default>
        <div class="rect2">
          <DxTextBox :show-clear-button="true" v-model:value="query" :placeholder="placeholder" :height="50" />
          <div v-if="query_type.value == 'trial'" class="examples">eg:
            <span @click="setQuery('NCT01277237')"> NCT01277237 </span>,
            <span @click="setQuery('Fatty Acids')"> Fatty Acids </span>
          </div>
          <div v-if="query_type.value == 'drug'" class="examples">eg:
            <span @click="setQuery('Acarbose')">Acarbose </span>,
            <span @click="setQuery('DB00163')">DB00163 </span>
          </div>
          <div v-if="query_type.value == 'target'" class="examples">eg:
            <span @click="setQuery('PRKAB1')"> PRKAB1 </span>,
            <span @click="setQuery('CASP1')"> CASP1 </span>,
            <span @click="setQuery('Peroxisome proliferator')"> Peroxisome proliferator </span>
          </div>
        </div>
      </template>
    </DxItem>
    <DxItem :ratio="3">
      <template #default>
        <div class="rect3">
          <DxButton type="danger" id="search" icon="search" @click="basicSearch()" :width="50" :height="50" />
        </div>
      </template>
    </DxItem>
  </DxBox>
</template>

<script>
import { Streamlit } from 'streamlit-component-lib';

import { DxBox, DxItem } from 'devextreme-vue/box';
import DxSelectBox from 'devextreme-vue/select-box';
import DxTextBox from 'devextreme-vue/text-box';
import DxButton from 'devextreme-vue/button';

const query_types = [{
  name: 'Clinical Trials',
  value: 'trial',
}, {
  name: 'Drugs',
  value: 'drug',
}, {
  name: 'Targets',
  value: 'target',
}]

export default {
  components: {
    DxSelectBox,
    DxBox,
    DxItem,
    DxTextBox,
    DxButton,
  },
  data() {
    return {
      query_types,
      query_type: query_types[0],
      query: '',
      placeholder: "search ...",
    };
  },
  methods: {
    onRender(event) {
      const data = event.detail
      console.log(data.args['name']);
      Streamlit.setFrameHeight()
    },
    basicSearch() {
      // console.log({
      //   query_type: this.query_type.value,
      //   query: this.query
      // })
      Streamlit.setComponentValue({
        query_type: this.query_type.value,
        query: this.query
      })
    },
    setQuery(e) {
      this.query = e
    }
  },
  updated() {
    Streamlit.setFrameHeight()
  },
  mounted() {
    Streamlit.events.addEventListener(Streamlit.RENDER_EVENT, this.onRender)
    Streamlit.setComponentReady()
    Streamlit.setFrameHeight()
  },
};
</script>

